.card {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-content: space-between;
  width: 200px;
  padding: 32px 16px 16px;

  border: 1px solid #e5e5e5;
  background-color: $color-white;

  &__image {
    width: 160px;
    height: 134px;
    margin: 0 auto;
  }

  &__name {
    margin-top: 40px;
    font-size: 12px;
    line-height: 18px;
    font-weight: 500;
    color: $color-main-accent;
  }

  &__product-code {
    margin-top: 4px;
    font-size: 10px;
    line-height: 14px;
    color: $color-secondary;
  }

  &__reviews {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 16px;
  }

  &__reviews-text {
    font-weight: 400;
    font-size: 10px;
  }

  &__price {
    margin-top: 24px;
    display: flex;
    justify-content: space-between;
  }

  &__price-label {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: $color-secondary;
  }

  &__price-number {
    font-weight: 700;
    line-height: 18px;
    color: $color-main-accent;
  }

  &__buy-link {
    box-sizing: border-box;
    margin-top: 16px;
    background-color: $color-blue-accent;
    color: $color-white;
    text-align: center;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 700;
    border: 1px solid transparent;
    border-radius: 5px;
    width: 100%;
    height: 40px;
    line-height: 40px;

    &:hover {
      color: $color-main-accent;
      background-color: $color-white;
      border-color: $color-blue-accent;
    }
  }
}
